h1, h2, h3, h4, a {
    font-family: 'Kumbh Sans', sans-serif;
}

body h1, h2, h3, h4, h5, h6, .h1, .h2, .h3, .h4, .h5, .h6 {
    font-weight: bold;
}

p {
    font-family: 'IBM Plex Serif', serif;
}

footer a {
    color: #40d092;
}

.card a {
    color: white;
}

.main-nav {
    background-color: black;
}


.hero {
    background-color: black;
    height: auto;
    padding: 6rem 0rem 9rem 0rem;
    color: white;
}

.hero h1, h2 {
    margin-bottom: 1.5rem;
}

#hero-text {
    display: inline-block;
    height: 100%;
}

img {
    width: 100%;
}

body .btn-primary {
    color: white;
    background-color: #40d092;
    border: #40d092;
}

.hero button {
    margin-top: 3rem;
}

.hero-cta {
    text-align: center;
    margin-top: -70px;
}

.hero-cta .card {
    background-color: #40d092;
    color: white;
    padding: 20px 0px;

}

.hero-cta .material-icons {
    font-size: 60px;
}

.bio {
    padding: 2rem 0rem;
}

#sidebar {
    box-shadow: 0 .5rem 1rem rgba(64, 208, 146, .5)!important;
    width: 100%;
}

.bio h3 {
    margin-top: 2rem;
}

.bio .row, h5, p {
    padding: 0rem 1rem;
}

.bio .material-icons {
    color: #40d092;
    margin: .25rem;
    font-size: 1em;
}

#bio-img {
    margin-bottom: 1rem;
}

li::marker {
    content: "â€¢  "; 
    color: #40d092;
    display: inline-block; 
    width: 2em;
    margin-right: 5rem;
}

footer {
    background-color: black;
    padding: 5rem;
    text-align: center;
}

footer span {
    color: white;
}

.shadow-img {
    box-shadow: 0 1rem 3rem rgba(0,0,0,.4);
}

.portfolio {
    padding: 2rem;
}

.sub {
    text-align: center;
}


.portfolio img {
    width: 100%;
}

.portfolio .row {
    padding: 3rem 0rem;
}

.portfolio h4 {
    line-height: 1.5;
    margin-bottom: 2rem;
}

#front-1 {
    max-width: 33%;
    z-index: 20;
    position: absolute;
    top: 1rem;
    left: 2rem;

}

#front-2 {
    max-width: 40%;
    z-index: 30;
    position: absolute;
    bottom: -6rem;
    right: -1rem;

}

#back-1 {
    max-width: 100%;
    z-index: 10;
    position: relative;
    bottom: -2rem;
}


#front-a {
    max-width: 15%;
    z-index: 40;
    position: absolute;
    bottom: -3rem;
    right: 4rem;

}

#front-b {
    max-width: 45%;
    z-index: 50;
    position: absolute;
    top: -1rem;
    right: 1rem;

}

#back-a {
    max-width: 80%;
    z-index: 30;
    position: relative;
    bottom: -2rem;
}

#bottom-row {
    margin-top: 5rem;
}

.contact {
    padding: 5rem 0rem;
}

@media (min-width: 992px) {

    .bio {
        padding: 10rem 5rem;
    }

    #sidebar {
        width: 80%;
    }

    .portfolio {
        padding: 10rem 5rem;
    }

    .portfolio .row {
        padding: 5rem 0rem;
    }
    
    #front-1 {
        max-width: 33%;
        z-index: 20;
        position: absolute;
        top: 2rem;
        left: 6rem;

    }

    #front-2 {
        max-width: 40%;
        z-index: 30;
        position: absolute;
        bottom: -10rem;
        right: -2rem;

    }

    #back-1 {
        max-width: 80%;
        z-index: 10;
        position: relative;
        right: -12rem;
        bottom: -4rem;
    }


    #front-a {
        max-width: 15%;
        z-index: 40;
        position: absolute;
        bottom: -3rem;
        right: 14rem;

    }

    #front-b {
        max-width: 45%;
        z-index: 20;
        position: absolute;
        top: -1rem;
        right: 6rem;

    }

    #back-a {
        max-width: 60%;
        z-index: 30;
        position: relative;
        bottom: -4rem;
        left: -2rem;
    }

    #bottom-row {
        margin-top: 10rem;
    }

    .contact .container {
        width: 50%!important;
    }
}

@media (max-width:576px) {
    .hero img {
        width: 90%;
        margin: auto;
        padding-top: 2rem;
    }

}